
body{
  background-color:darkgray;
}
.Main{
  align-items: center;
  justify-content: center;
  text-align: center;
  color: rgb(17, 4, 4);

}
.Box1{
  background-color: #61dafb;
  background: url(./IMG/main-bag.jpg)no-repeat;
  background-size: cover;
  border-radius: 5px;
  box-shadow: 0 0 20px black;
  width: 500px;
  margin: auto;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 520px;
  padding-bottom: 5px;
  margin-top: 30px;
}
.btnpart{
  height: 65px;
}
.Input{
  width: 90%;
  height: 40px;
  border-radius: 5px;
  border: 3px solid rgb(0, 255, 179);
  box-shadow: 0 0 20px rgb(0, 255, 179);
}
.btn1{
  margin-top: 10px;
  border: 1px solid #61fbcf;
    border-radius: 3px;
    width: 100px;
    font-weight: bold;
    height: 40px;
    color: #1df3b6;
    background: linear-gradient(to left, transparent 50%, #00abf0 50%) right;
    background-size: 200%;
    transition: .5s ease-out;
    box-shadow: 0 0 20px #61fbcf;
}
.btn2{
  margin-left: 45%; 
  border: 1px solid #61fbcf;
    border-radius: 3px;
    width: 100px;
    font-weight: bold;
    height: 40px;
    color: #1df3b6;
    background: linear-gradient(to left, transparent 50%, #00abf0  50%) right;
    background-size: 200%;
    transition: .5s ease-out;
    box-shadow: 0 0 20px #61fbcf;
}
.btn1:hover{
  background-position: left;
  box-shadow: 0px 0px 30px rgba(247, 148, 165, 0.793),0 0px 50px rgba(247, 148, 165, 0.793);
  color: black;
  border: 1px solid #00abf0;
}
.btn2:hover{
  background-position: left;
  box-shadow: 0px 0px 30px rgba(247, 148, 165, 0.793),0 0px 50px rgba(247, 148, 165, 0.793);
  color: black;
  border: 1px solid #00abf0;
}

.City{
  font-size: 25px;
  font-weight: 400;
 
}
.day{
  font-size: 25px; 
}
.main-text{
  margin-top: 20px;
  font-weight: 700;
}
.Img1{
  height: 100px;
}
.line1{
  align-items: center;
  justify-content: center;
  display: flex;
  height: 100px;
}
.temp{
  margin-left: 10%;
  font-size: 45px;
  position: relative;
  font-weight: 400;
}
.description{
  margin-top: 10px;
  margin-right: 35%;
  font-size: 25px;
}
/*this part is for cumpo2*/
.my-text{
  text-align: center;
  font-size: 30px;
  margin-top: 10px;
  font-weight: 700;
  filter: drop-shadow(0 10px 5px rgb(87, 231, 204));
}
.InerBox{
  background-color: aquamarine;
  border-radius: 5px;
  box-shadow: 0 0 20px black;
  width: 500px;
  margin: auto;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 25px;
}
.nodata{
  text-align: center;
  margin-top: 50%;
}
.Img2{
  height: 100px;
}
.forecastdate{
  font-size: 30px;
  font-weight: 200;
  margin-left: 20%;
}
.display{
  display: flex;
  flex-direction: row;
  background: #000;
  height: 500px;
}

.innerdiv{
  background-color: dodgerblue;
  padding: 10px;
  flex: 50%;
}
.temp2{
  font-size: 35px;
  font-weight: 400;
}
.lowerline1{
  justify-content: center;
  display: flex;
}


/*meadia query*/

@media only screen and (max-width: 600px){
  .Box1{
    margin: auto;
    width: 400px;
    margin-top: 30px;
  }
  .InerBox{
    margin: auto;
    width: 400px;
    margin-top: 25px;
  }

}
@media(320px <= width <= 374px){
  .Box1{
    margin: auto;
    width: 300px;
    margin-top: 30px;
    
  }
  .InerBox{
    margin: auto;
    width: 300px;
    margin-top: 25px;
  }
  .btn2{
    margin-left: 25%;
  }
  .forecastdate{
    margin-left: 2%;
  }
}
@media(375px <= width <= 424px){
  .Box1{
    margin: auto;
    width: 350px;
    margin-top: 30px;
    
  }
  .InerBox{
    margin: auto;
    width: 350px;
    margin-top: 25px;
  }
  .btn2{
    margin-left: 30%;
  }
  .forecastdate{
    margin-left: 5%;
  }
}


.lowbox {
  display: flex;
  flex-wrap: wrap;
